}

.dynamic-leave-active {
  transition: all 0.5s ease;
}

.dynamic-enter,
.dynamic-leave-to {
  opacity: 0;
  transform: translateY(100%);
}

.mask {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 3;
}

.Calendar-mask {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 4;
}
</style>



